.category-container {
    display: flex;
    width: 312px;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: center;
    margin: 34px auto 34px auto;
}

.category-header {
    display: flex;
    width: 100%;
    margin-bottom: 12px;
}

span { 
    width: 4px;
    height: 31px;
    background-color: #171738;
}

.category-name {
    width: 302px;
    text-align: left;
    line-height: 31px;
    margin-left: 6px;
    font-size: 25px;
    font-weight: 800;
}

.item-container {
    width: 149px;
    height: 196px;
    margin: 0px 0px 26px 0px;
}

.item-img {
    position: relative;
    height: 149px;
}

.heart {
    position: absolute;
    top: 8px;
    right: 7px;
    z-index: 1;
}

.shop-now {
    position: absolute;
    bottom: 0px;
    right: 0px;
    background-color: #E4572E;
    color: white;
    box-shadow: -3px -3px 3px 0px transparent;
    border-top-left-radius: 10px;
    width: 72px;
    height: 24px;
    font-size: 12px;
    font-weight: 600;
    line-height: 24px;
    z-index: 1;
}

.item-info {
    margin-top: 5px;
}

.item-name {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 400;
}

.item-price {
    font-size: 14px;
    font-weight: 800;
}