.carousel {
    width: 100%;
}

.carousel img {
    width: 100%;
}

.carousel-nav-container {
    display: flex;
    margin: auto;
    position: relative;
    justify-content: center;
}

.carousel-nav {
    position: absolute;
    bottom: 20px;
    z-index: 1;
    height: 13px;
    line-height: 13px;
    display: flex;
    align-items: center;
}

.carousel-nav span {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin: 0px 3px 0px 3px;
    border-radius: 50%;
    background-color: rgb(60,60,60);
}

.carousel-nav span.carousel-nav-active {
    background-color: white;
    width: 13px;
    height: 13px;
}